<template>
  <view class="page">

    <!-- 切换地址 -->
    <view class="change-btn" v-if="is_public_address">
      <view :class="addressType==1?'active':''" @click='gotoAddress(1)'>个人</view>
      <view :class="addressType==2?'active':''" @click='gotoAddress(2)'>公益</view>
    </view>

    <!-- 地址 -->
    <view @click='gotoAddress'>
      <address-item v-if="user_checked_address.id" :address="user_checked_address"></address-item>
      <view class="add-address" v-else>+ 添加地址</view>
    </view>

    <!-- 商品列表 -->
    <block v-for="(item,i) in cart_list" :key='i'>
      <goods-item :goods="item" v-show='item.is_checked' :showRadio="false" :showNumBox='false' :showNum='true'>
      </goods-item>
    </block>
    <view class="sub">
      <view>共{{checkedCount}}件</view>
      <view>小计:￥<text>{{checkedGoodsAmount}}</text></view>
    </view>

    <!-- 优惠券 -->
    <view class="coupons-box">
      <!-- <view class="item">
        <view class="title">小计</view>
        <view class="discount">￥ {{checkedGoodsAmount}}</view>
      </view> -->
      <view class="item">
        <view class="title">优惠券</view>
        <view class="discount" v-if="coupon!==0" @click="showCouponsPopup">-￥ {{coupon}}</view>
        <view class="else" v-else @click="showCouponsPopup">请选择<uni-icons type="arrowright" size="12" color="">
          </uni-icons>
        </view>
      </view>
      <view class="item">
        <view class="title">邮费</view>
        <view class="discount">￥ {{postage}}</view>
      </view>
    </view>

    <!-- 提交订单按钮 -->
    <view class="submit-box">
      <view class="container">
        <view class="price">
          合计:<text>￥{{totalPrice}}</text>
        </view>
        <view class="btn" @click="onSubmit">提交订单</view>
      </view>
    </view>

    <!-- 优惠券弹出层 -->
    <uni-popup ref="popup" type="bottom">
      <view class="top">优惠券</view>
      <scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y" v-if="userCouponsList.length>0">
        <view class="scroll-item" v-for="(item,i) in userCouponsList" :key='i' @click="couponChanged(item)">
          <view class="left">
            <view>￥<text>{{item.price}}</text></view>
            <view>满{{item.min_price}}可用</view>
          </view>
          <view class="right">
            <view>会员减10元优惠券</view>
            <view>不限时</view>
            <!-- <view v-else>不可用</view> -->
          </view>
        </view>
      </scroll-view>
      <view class="no-coupon" v-else>
        <image src="/static/images/no-coupon.png" mode="widthFix"></image>
      </view>
    </uni-popup>

  </view>
</template>

<script>
  import {
    mapState,
    mapGetters,
    mapMutations,
    createNamespacedHelpers
  } from 'vuex'
  const {
    mapGetters: mapGettersCart,
    mapState: mapStateCart
  } = createNamespacedHelpers('cart')
  const {
    // mapGetters: mapGettersAddress,
    mapState: mapStateAddress
  } = createNamespacedHelpers('address')

  export default {
    data() {
      return {
        addressType: 1, // 地址类型
        postage: 10,
        coupon: 0,
        user_checked_address: {},
        // checkedAddress: {},
        userCouponsList: [{
            name: '优惠券-10元',
            price: 10,
            min_price: 100
          },
          {
            name: '优惠券-20元',
            price: 20,
            min_price: 200
          }, {
            name: '优惠券-30元',
            price: 30,
            min_price: 300
          }, {
            name: '优惠券-40元',
            price: 40,
            min_price: 400
          }, {
            name: '优惠券-50元',
            price: 50,
            min_price: 500
          }, {
            name: '优惠券-60元',
            price: 60,
            min_price: 600
          },
        ],
        // userCouponsList: [],
      };
    },
    onLoad(option) {
      console.log(this.user_checked_address)
    },
    onShow() {
      this.checkedAddress()
      this.couponChanged()
    },
    onUnload() {
      uni.removeStorageSync('user_checked_address')
    },

    computed: {
      ...mapStateCart(['cart_list']),
      ...mapStateAddress(['address_list', 'is_public_address']),
      ...mapGettersCart(['checkedCount', 'checkedGoodsAmount']),

      // 合计
      totalPrice() {
        return (Number(this.checkedGoodsAmount) + Number(this.postage) - Number(this.coupon)).toFixed(2)
      },

      // // 优惠券
      // coupon(val) {
      //   console.log(val)
      //   const filterRes = this.userCouponsList.filter(item => item.min_price <= this.checkedGoodsAmount)
      //   if (filterRes.length == 0) return 0
      //   // console.log(filterRes)
      //   return 30
      // }

    },

    methods: {
      ...mapMutations('address', ['editCheckedAddress']),

      // 展示优惠券弹出层
      showCouponsPopup() {
        this.$refs.popup.open('bottom')
      },

      // 切换优惠券
      couponChanged(item) {
        if (!item) {
          // 初始化
          const filterRes = this.userCouponsList.filter(item => item.min_price <= this.checkedGoodsAmount)
          // console.log(filterRes.length)
          if (filterRes.length == 0) return this.coupon = 10
          // console.log(filterRes)
          filterRes.forEach((item, i) => {
            // console.log(filterRes[i].price)
            // if (filterRes[i].price < filterRes[i + 1].price) this.coupon = filterRes[i + 1].price
            if (item.price > this.coupon) this.coupon = item.price
          })
          // return this.coupon = 30
        } else {
          // 切换
          if (this.checkedGoodsAmount >= item.min_price) {
            this.coupon = item.price
            this.$refs.popup.close()
          } else {
            this.coupon = 0
            return uni.$showMsg(`商品总价小于${item.min_price}元`)
          }

        }
      },

      // 收货地址
      checkedAddress() {
        var checked_address_stroge = uni.getStorageSync('user_checked_address')
        if (checked_address_stroge) {
          this.user_checked_address = JSON.parse(checked_address_stroge)
        } else {
          this.user_checked_address = this.address_list.filter(x => x.is_default)[0]
        }
      },

      // 提交订单
      onSubmit() {
        // this.editCheckedAddress()
        console.log(this.user_checked_address)
        //   console.log(this.defaultAddress)
      },

      gotoAddress(type) {
        if (type === 1) {
          uni.navigateTo({
            url: '/mypkg/address/address?from=settle&type=' + this.addressType
          })
          this.addressType = 2
        } else {
          uni.navigateTo({
            url: '/mypkg/address/address?from=settle&type=' + this.addressType
          })
          this.addressType = 1
        }
      }
    },

  }
</script>

<style lang="scss">


  .page {
    padding: 10px 0;

    >button {
      margin: 0 10px 10px;
      background-color: #FFFFFF;

      &::after {
        border: 0;
      }
    }
  }

  .change-btn {
    display: flex;
    width: 88px;
    font-size: 14px;
    margin: 0 0 10px 10px;
    border: 1px solid #C0C8D3;
    border-radius: 3px;

    >view {
      padding: 6rpx 16rpx;
      color: #C0C8D3;

      &.active {
        color: #FFFFFF;
        background-color: #C0C8D3;
      }
    }
  }

  .radius {
    border-radius: 5px !important;
    overflow: hidden;
  }

  .address-item {
    margin: 0 10px 10px !important;
  }

  .add-address {
    text-align: center;
    padding: 20px;
    margin: 0 10px 10px;
    color: #666;
    background-color: #FFFFFF;
    border-radius: 5px;
  }

  .goods-item {
    margin: 0 10px;
    // border-top: 1px solid #eee;
  }

  .sub {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin: 0 10px 0;
    padding: 20px 10px 10px;
    font-size: 12px;

    background-color: #FFFFFF;
    border-radius: 5px;

    view {
      margin-left: 10px;

      text {
        font-size: 14px;
        color: tomato;
      }
    }
  }

  .coupons-box {
    margin: 10px;
    padding-bottom: 10px;
    background-color: #FFFFFF;
    border-radius: 5px;
    overflow: hidden;

    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 15px 0;
      background-color: #FFFFFF;

      .discount {
        flex: 1;
        text-align: right;
        font-size: 12px;
        color: tomato;
      }

      .else {
        flex: 1;
        text-align: right;
        font-size: 12px;
        color: #ccc;
      }

    }
  }

  .submit-box {
    position: fixed;
    bottom: 0;
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);

    width: 100%;
    background-color: #FFFFFF;

    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 50px;
      padding: 0 15px;
      box-sizing: border-box;
      // background-color: #007AFF;

      .count {
        font-size: 14px;
        color: #C0C8D3;
      }

      .price {
        display: flex;
        align-items: center;
        font-size: 15px;

        text {
          font-size: 18px;
          color: tomato;
        }
      }

      .btn {
        height: 40px;
        line-height: 40px;
        padding: 0 20px;
        margin-left: 10px;
        color: #FFFFFF;
        background-color: tomato;
        border-radius: 20px;
      }
    }
  }

  // 优惠券弹出层
  .vue-ref {
    background-color: #fff;
    height: 450px;
    // padding: 0 10px;
    border-radius: 10px 10px 0 0;
    overflow: hidden;

    .top {
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-weight: bold;
      background-color: #FFFFFF;
    }

    scroll-view,
    .no-coupon {
      height: 350px;
    }

    scroll-view {
      width: 100%;
      padding: 20rpx 20rpx 0;
      background-color: #f5f5f5;
      box-sizing: border-box;

      .scroll-item {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        height: 180rpx;

        .left {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 220rpx;
          height: 100%;
          text-align: center;
          font-weight: bold;
          color: #FFFFFF;
          font-size: 36rpx;

          view:nth-child(2) {
            font-size: 24rpx !important;
            font-weight: 400;
          }

          text {
            font-size: 60rpx;
          }
        }

        .right {
          margin-left: 20px;
          font-size: 28rpx;
          line-height: 40rpx;

          view:nth-child(2) {
            font-size: 24rpx !important;
            color: #ccc;
          }
        }

        background: url(data:image/png;base64,) no-repeat;
        background-size: 100% 100%;
      }

    }

    .no-coupon {
      display: flex;
      justify-content: center;
      align-items: center;

      // line-height: 400px;
      // text-align: center;
      image {
        width: 60%;
      }
    }
  }
</style>
